// Theme configuration - Light, warm, approachable
export const theme = {
  // Colors - Light theme with warmth
  accentColor: '#6366F1', // Soft indigo accent
  accentColorLight: '#818CF8', // Lighter hover variant
  textColor: '#1F2937', // Warm dark gray for text
  textColorLight: '#6B7280', // Muted gray for secondary text
  backgroundColor: '#FAFAF9', // Warm off-white background
  backgroundDark: '#F5F5F4', // Slightly darker warm background
  cardBackground: '#FFFFFF', // Clean white cards
  surfaceRaised: '#F9FAFB', // Subtle raised surface
  borderColor: 'rgba(0,0,0,0.06)',
  
  // Typography - Clean, professional, approachable
  // Swap these in one place (these also drive global CSS variables)
  fontFamily: '"Inter"', // Headings / UI - clean and modern
  bodyFontFamily: '"Inter"', // Body copy - consistent and readable
  
  // Font sizes - Intentional hierarchy
  fontSize: {
    hero: '3.75rem',      // 60px - restrained, not gigantic
    h1: '2.5rem',         // 40px
    h2: '1.875rem',       // 30px
    h3: '1.5rem',         // 24px
    body: '1.0625rem',    // 17px - slightly larger than default
    small: '0.9375rem',   // 15px
  },
  
  // Spacing & Layout - Asymmetric, intentional
  maxWidth: '1140px',        // Narrower constraint
  sectionSpacing: {
    tight: '4rem',           // 64px
    normal: '6rem',          // 96px
    loose: '8rem',           // 128px
  },
  borderRadius: '12px',
  cardBorderRadius: '16px',
  
  // Shadows - Soft and subtle (light-mode tuned)
  shadowSm: '0 1px 2px rgba(0, 0, 0, 0.04)',
  shadowMd: '0 4px 12px rgba(0, 0, 0, 0.08)',
  shadowLg: '0 12px 24px rgba(0, 0, 0, 0.12)',
  
  // Tag styling
  tagBackground: 'rgba(99, 102, 241, 0.10)',
  tagColor: '#4F46E5',
  
  // Animations - Friendly and interactive
  transitionSpeed: '0.2s',
  hoverScale: '1.02', // Slight scale on hover - more interactive
};

// Helper function to get theme values
export const getThemeValue = (key) => theme[key];

// Apply theme to global CSS variables (keeps customization centralized)
export const applyThemeToDocument = (t = theme) => {
  if (typeof document === 'undefined') return;
  const root = document.documentElement;

  const hexToRgb = (hex) => {
    const normalized = hex.replace('#', '').trim();
    const full =
      normalized.length === 3
        ? normalized
            .split('')
            .map((c) => c + c)
            .join('')
        : normalized;
    const int = Number.parseInt(full, 16);
    if (Number.isNaN(int) || full.length !== 6) return null;
    return { r: (int >> 16) & 255, g: (int >> 8) & 255, b: int & 255 };
  };

  const accentRgb = hexToRgb(t.accentColor);
  const accentSoft = accentRgb
    ? `rgba(${accentRgb.r}, ${accentRgb.g}, ${accentRgb.b}, 0.10)`
    : 'rgba(91, 95, 199, 0.10)';

  root.style.setProperty('--color-accent', t.accentColor);
  root.style.setProperty('--color-accent-soft', accentSoft);
  root.style.setProperty('--color-text', t.textColor);
  root.style.setProperty('--color-text-muted', t.textColorLight);
  root.style.setProperty('--color-bg', t.backgroundColor);
  root.style.setProperty('--color-card', t.cardBackground);
  root.style.setProperty('--color-border', t.borderColor || 'rgba(255,255,255,0.10)');

  root.style.setProperty(
    '--font-heading',
    `${t.fontFamily}, -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif`
  );
  root.style.setProperty(
    '--font-body',
    `${t.bodyFontFamily}, ui-serif, Georgia, "Times New Roman", serif`
  );
};
