import React from 'react';
import Hero from '../components/Hero';
import { Link } from 'react-router-dom';
import { theme } from '../data/theme';
import { getAllProjects } from '../data/projects';
import { GlowingEffect } from '../components/ui/glowing-effect';

const Home = () => {
  const allProjects = getAllProjects();
  const flagship = allProjects.filter((p) => p.flagship);
  const featured = allProjects.filter((p) => p.featured && !p.flagship);
  const experiments = allProjects.filter((p) => !p.featured);

  return (
    <div style={{ backgroundColor: theme.backgroundColor }}>
      {/* Hero Section (ContainerScroll is used only here) */}
      <section>
        <Hero />
      </section>

      {/* Immediate transition into work */}
      <section className="pb-24 -mt-10">
        <div className="mx-auto px-6 sm:px-8 lg:px-12" style={{ maxWidth: theme.maxWidth }}>
          <div className="flex items-end justify-between gap-8 mb-12">
            <div className="max-w-xl">
              <div
                className="text-xs font-semibold uppercase tracking-wider mb-3"
                style={{ color: theme.textColorLight, fontFamily: theme.fontFamily }}
              >
              </div>
              <h2
                className="text-3xl md:text-4xl font-semibold"
                style={{ color: theme.textColor, fontFamily: theme.fontFamily, lineHeight: '1.15' }}
              >
                Some projects I've worked on
              </h2>
            </div>
          </div>

          {/* Asymmetric flagship grid */}
          <div className="grid grid-cols-12 gap-6 lg:gap-8">
            {flagship[0] && (
              <div className="col-span-12 lg:col-span-7">
                <GlowingEffect glowColor="rgba(107, 127, 255, 0.28)" borderRadius="18px">
                  <div
                    className="rounded-2xl overflow-hidden"
                    style={{
                      backgroundColor: theme.cardBackground,
                      border: `1px solid ${theme.borderColor}`,
                      borderRadius: theme.cardBorderRadius,
                    }}
                  >
                            {/* Image on top - full width */}
                            {flagship[0].image && (
                              <div className="relative overflow-hidden bg-gray-50" style={{ aspectRatio: '16/9' }}>
                                <img
                                  src={flagship[0].image}
                                  alt={flagship[0].title}
                                  className="w-full h-full object-contain p-4"
                                  draggable={false}
                                />
                              </div>
                            )}
                    
                            {/* Content below */}
                            <div className="p-8 md:p-9">
                              <div className="flex items-start justify-between gap-4 mb-3">
                                <div className="flex-1">
                                  <div
                                    className="text-xs font-semibold uppercase tracking-wide"
                                    style={{ color: theme.accentColor, fontFamily: theme.fontFamily }}
                                  >
                                    {flagship[0].context}
                                  </div>
                                </div>
                                {flagship[0].link && (
                                  <a
                                    href={flagship[0].link}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    className="flex-shrink-0 p-2 rounded-lg hover:bg-gray-100 transition-all hover:scale-110 duration-200"
                                    style={{ color: theme.accentColor }}
                                    title="View project"
                                  >
                                    <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                                      <path d="M18 13v6a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V8a2 2 0 0 1 2-2h6"></path>
                                      <polyline points="15 3 21 3 21 9"></polyline>
                                      <line x1="10" y1="14" x2="21" y2="3"></line>
                                    </svg>
                                  </a>
                                )}
                              </div>
                              <h3
                                className="text-2xl md:text-3xl font-semibold mb-4"
                                style={{ color: theme.textColor, fontFamily: theme.fontFamily, lineHeight: '1.15' }}
                              >
                                {flagship[0].title}
                              </h3>
                      <p
                        className="text-[1.02rem] leading-relaxed"
                        style={{ color: theme.textColorLight, fontFamily: theme.bodyFontFamily }}
                      >
                        {flagship[0].summary}
                      </p>
                      {flagship[0].tags?.length ? (
                        <div className="flex flex-wrap gap-2 mt-6">
                          {flagship[0].tags.slice(0, 6).map((tag, idx) => (
                            <span
                              key={idx}
                              className="px-3 py-1 text-xs rounded-md"
                              style={{ backgroundColor: theme.tagBackground, color: theme.tagColor, fontFamily: theme.fontFamily }}
                            >
                              {tag}
                            </span>
                          ))}
                        </div>
                      ) : null}
                    </div>
                  </div>
                </GlowingEffect>
              </div>
            )}

            {flagship[1] && (
              <div className="col-span-12 lg:col-span-5 lg:mt-12">
                <GlowingEffect glowColor="rgba(107, 127, 255, 0.22)" borderRadius="18px">
                  <div
                    className="rounded-2xl overflow-hidden"
                    style={{
                      backgroundColor: theme.cardBackground,
                      border: `1px solid ${theme.borderColor}`,
                      borderRadius: theme.cardBorderRadius,
                    }}
                  >
                    {/* Image on top */}
                    {flagship[1].image && (
                      <div className="relative overflow-hidden bg-gray-50" style={{ aspectRatio: '16/9' }}>
                        <img
                          src={flagship[1].image}
                          alt={flagship[1].title}
                          className="w-full h-full object-contain p-4"
                          draggable={false}
                        />
                      </div>
                    )}
                    
                        {/* Content below */}
                        <div className="p-7 md:p-8">
                          <div className="flex items-start justify-between gap-4 mb-3">
                            <div className="flex-1">
                              <div
                                className="text-xs font-semibold uppercase tracking-wide"
                                style={{ color: theme.accentColor, fontFamily: theme.fontFamily }}
                              >
                                {flagship[1].context}
                              </div>
                            </div>
                            {flagship[1].link && (
                              <a
                                href={flagship[1].link}
                                target="_blank"
                                rel="noopener noreferrer"
                                className="flex-shrink-0 p-2 rounded-lg hover:bg-gray-100 transition-all hover:scale-110 duration-200"
                                style={{ color: theme.accentColor }}
                                title="View project"
                              >
                                <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                                  <path d="M18 13v6a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V8a2 2 0 0 1 2-2h6"></path>
                                  <polyline points="15 3 21 3 21 9"></polyline>
                                  <line x1="10" y1="14" x2="21" y2="3"></line>
                                </svg>
                              </a>
                            )}
                          </div>
                          <h3
                            className="text-2xl font-semibold mb-3"
                            style={{ color: theme.textColor, fontFamily: theme.fontFamily, lineHeight: '1.15' }}
                          >
                            {flagship[1].title}
                          </h3>
                      <p className="leading-relaxed" style={{ color: theme.textColorLight, fontFamily: theme.bodyFontFamily }}>
                        {flagship[1].summary}
                      </p>
                    </div>
                  </div>
                </GlowingEffect>
              </div>
            )}
          </div>

          {/* Secondary highlight (Penn Vet) */}
          {featured[0] && (
            <div className="mt-20 grid grid-cols-12 gap-6 lg:gap-8 items-start">
              <div className="col-span-12 md:col-span-4">
                <h3 className="text-xl md:text-2xl font-semibold" style={{ color: theme.textColor, fontFamily: theme.fontFamily }}>
                  {featured[0].title}
                </h3>
              </div>
              <div className="col-span-12 md:col-span-8">
                <div
                  className="rounded-2xl p-7 md:p-8"
                  style={{ backgroundColor: theme.cardBackground, border: `1px solid ${theme.borderColor}`, borderRadius: theme.cardBorderRadius }}
                >
                  <div className="flex items-start justify-between gap-4 mb-3">
                    <div className="text-sm flex-1" style={{ color: theme.textColorLight, fontFamily: theme.fontFamily }}>
                      {featured[0].context}
                    </div>
                    {featured[0].link && (
                      <a
                        href={featured[0].link}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="flex-shrink-0 p-2 rounded-lg hover:bg-gray-100 transition-all hover:scale-110 duration-200"
                        style={{ color: theme.accentColor }}
                        title="View project"
                      >
                        <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                          <path d="M18 13v6a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V8a2 2 0 0 1 2-2h6"></path>
                          <polyline points="15 3 21 3 21 9"></polyline>
                          <line x1="10" y1="14" x2="21" y2="3"></line>
                        </svg>
                      </a>
                    )}
                  </div>
                  <p className="text-[1.02rem] leading-relaxed" style={{ color: theme.textColorLight, fontFamily: theme.bodyFontFamily }}>
                    {featured[0].summary}
                  </p>
                  {featured[0].tags?.length ? (
                    <div className="flex flex-wrap gap-2 mt-6">
                      {featured[0].tags.map((tag, idx) => (
                        <span
                          key={idx}
                          className="px-3 py-1 text-xs rounded-md"
                          style={{ backgroundColor: theme.tagBackground, color: theme.tagColor, fontFamily: theme.fontFamily }}
                        >
                          {tag}
                        </span>
                      ))}
                    </div>
                  ) : null}
                </div>
              </div>
            </div>
          )}

          {/* Experiments preview */}
          {experiments.length > 0 && (
            <div className="mt-20 pt-20" style={{ borderTop: `1px solid ${theme.borderColor}` }}>
              <div className="flex items-baseline justify-end gap-8 mb-8">
                <Link
                  to="/projects"
                  className="text-sm font-medium hover:opacity-70 transition-opacity"
                  style={{ color: theme.accentColor, fontFamily: theme.fontFamily }}
                >
                  View all projects 
                </Link>
              </div>
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {experiments.slice(0, 3).map((p) => {
                  // Determine if image is vertical (Snap Spectacles or FPL)
                  const isVerticalImage = p.id === 5 || p.id === 6;
                  
                  return (
                    <div
                      key={p.id}
                      className="rounded-2xl overflow-hidden h-full"
                      style={{
                        backgroundColor: theme.cardBackground,
                        borderRadius: theme.cardBorderRadius,
                        border: `1px solid ${theme.borderColor}`,
                      }}
                    >
                      {/* Image on top */}
                      {p.image && (
                        <div className="relative overflow-hidden bg-gray-50" style={{ aspectRatio: '16/9' }}>
                          <img
                            src={p.image}
                            alt={p.title}
                            className={`w-full h-full ${isVerticalImage ? 'object-cover' : 'object-contain p-3'}`}
                            draggable={false}
                            style={isVerticalImage ? { objectPosition: 'center' } : {}}
                          />
                        </div>
                      )}

                      {/* Content below */}
                      <div className="p-6">
                        <div className="flex items-start justify-between gap-4 mb-2">
                          <div className="flex-1">
                            {p.context && (
                              <div
                                className="text-xs uppercase tracking-wide"
                                style={{ color: theme.textColorLight, fontFamily: theme.fontFamily }}
                              >
                                {p.context}
                              </div>
                            )}
                          </div>
                          {p.link && (
                            <a
                              href={p.link}
                              target="_blank"
                              rel="noopener noreferrer"
                              className="flex-shrink-0 p-1.5 rounded-lg hover:bg-gray-100 transition-all hover:scale-110 duration-200"
                              style={{ color: theme.accentColor }}
                              title="View project"
                            >
                              <svg
                                xmlns="http://www.w3.org/2000/svg"
                                width="18"
                                height="18"
                                viewBox="0 0 24 24"
                                fill="none"
                                stroke="currentColor"
                                strokeWidth="2"
                                strokeLinecap="round"
                                strokeLinejoin="round"
                              >
                                <path d="M18 13v6a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V8a2 2 0 0 1 2-2h6"></path>
                                <polyline points="15 3 21 3 21 9"></polyline>
                                <line x1="10" y1="14" x2="21" y2="3"></line>
                              </svg>
                            </a>
                          )}
                        </div>

                        <h3
                          className="text-lg font-semibold mb-2"
                          style={{ color: theme.textColor, fontFamily: theme.fontFamily, lineHeight: '1.3' }}
                        >
                          {p.title}
                        </h3>

                        <p
                          className="text-sm mb-4 line-clamp-4"
                          style={{ color: theme.textColorLight, fontFamily: theme.bodyFontFamily }}
                        >
                          {p.summary}
                        </p>

                        {p.tags && p.tags.length > 0 && (
                          <div className="flex flex-wrap gap-2">
                            {p.tags.map((tag, index) => (
                              <span
                                key={index}
                                className="px-3 py-1 text-xs rounded-full border"
                                style={{
                                  backgroundColor: theme.tagBackground,
                                  borderColor: 'rgba(107,127,255,0.22)',
                                  color: theme.tagColor,
                                  fontFamily: theme.fontFamily,
                                }}
                              >
                                {tag}
                              </span>
                            ))}
                          </div>
                        )}
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>
          )}

          {/* Subtle CTA (not a big centered button) */}
          <div className="mt-20">
            <Link
              to="/projects"
              className="inline-flex items-center gap-2 text-sm font-medium hover:opacity-70 transition-opacity"
              style={{ color: theme.textColor, fontFamily: theme.fontFamily }}
            >
              View all work <span aria-hidden="true"></span>
            </Link>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Home;
